.card {
    box-sizing: border-box;
    width: 190px;
    height: 254px;
    background: rgba(217, 217, 217, 0.58);
    border: 1px solid white;
    box-shadow: 12px 17px 51px rgba(0, 0, 0, 0.22);
    backdrop-filter: blur(6px);
    border-radius: 17px;
    text-align: center;
    cursor: pointer;
    transition: all 0.5s;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    font-weight: bolder;
  }
  
  .card:hover {
    border: 1px solid black;
    transform: scale(1.05);
  }
  
  .card:active {
    transform: scale(0.95) rotateZ(1.7deg);
  }

  .imgContainer{
    width: 100%;
    display: flex;
    justify-content: center;
    justify-items: center;
    padding: 0;
    margin: 0;
    top:5%;
    position: absolute;
  }
  .imgContainer img{
    border-radius: 1rem;
    width: 90%;
    max-height: 160px;
  }
  .title {
    font-size: 1.5em;
    font-weight: 900;
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top:150px;
  }
  .subtitle{
    color: rgba(0, 0, 0, 0.603);}